.l_logo{
	width:35%; overflow:hidden; margin:20% auto;
}
.l_logo img{
	max-width:100%;
}
.login_bg{
	overflow:hidden;
}
.log_tab{
	background-color:#FFF; border-top:1px solid #eaeaea; border-bottom:1px solid #eaeaea;
}
.log_c{
	width:91%; overflow:hidden; padding:2% 0; border-bottom:1px solid #eaeaea;
}
.log_c:last-child{
	border-bottom:0;
}
.log_c table{
	border:"0"; width:100%; font-size:1.4em; border-spacing: 0; border-collapse: collapse;
}
.log_c table tbody tr td:nth-of-type(1){
	width: 3.4rem;
}
.log_c table tbody tr td:nth-of-type(2){
	width: 5rem;
	text-indent:8px
}
.log_c_img{
	width:40px; height:30px; background:url(/image/l_line.gif) no-repeat right center; padding:5px 0;
}
.l_ipt{
	width:98%; height:40px; line-height:40px\9px; overflow:hidden; color:#999999; font-size:1em; font-family:"Microsoft YaHei"; padding:0; border:0;
}
.l_btn{
	width:100%; height:45px; line-height:45px\9; overflow:hidden; background-color:#ff4e00; 
	color:#FFF; font-size:1.3em; font-family:"Microsoft YaHei"; text-align:center; border:0; border-radius:2px; -mos-border-radius:2px; -webkit-border-radius:2px;
}
.l_tel{
	height:35px; line-height:35px; overflow:hidden; background-color:#ff4e00; color:#FFF; font-size:1em; text-align:center; float:right; padding:0 3%; border-radius:2px; -mos-border-radius:2px; -webkit-border-radius:2px; position:absolute; right:0; top:5%;
}
.l_tel a{
	color:#FFF;
}

.lastTab{
	border:"0";width:91%; margin:5% auto 0 auto; border-spacing: 0; border-collapse: collapse;
}
.lastTab label{
	font-size:1.4em;
}