.btm{
	overflow:hidden; text-align:center; padding:2% 5% 0 5%; margin-top:2%; border-top:1px solid #cccccc;
}

.b_btm{
	width:100%; overflow:hidden; background-color: #333; margin-top:5%; border-top:1px solid #eaeaea;position: relative ;bottom: 0;height: 7rem;
}
.b_btm ul li{
	width:22%; height:80px; overflow:hidden; color:#333333; font-size:1.4em; text-align:center; float:left; margin:2% 1% 2% 2%;
}
.b_btm ul li a{
	width:100%; height:88px; overflow:hidden; color:#fff; float:left; text-align:center;
}
.b_btm ul li a:hover, .b_btm ul li:hover a, .b_btm ul li.cur a{
	color:#ff4e00;
}
.b_btm ul li p{
	height:40px; overflow:hidden; margin:5% auto 3% auto; padding:0;
}
.b_btm ul li.b_1 p{
	background:url(/image/b_1.png) no-repeat center center;
}
.b_btm ul li.b_1:hover p, .b_btm ul li.b_1.cur p{
	background:url(/image/b_1_h.png) no-repeat center center;
}	
.b_btm ul li.b_2 p{
	background:url(/image/b_2.png) no-repeat center center;
}
.b_btm ul li.b_2:hover p, .b_btm ul li.b_2.cur p{
	background:url(/image/b_2_h.png) no-repeat center center;
}
.b_btm ul li.b_3 p{
	background:url(/image/b_3.png) no-repeat center center;
}
.b_btm ul li.b_3:hover p, .b_btm ul li.b_3.cur p{
	background:url(/image/b_3_h.png) no-repeat center center;
}
.b_btm ul li.b_4 p{
	background:url(/image/b_4.png) no-repeat center center;
}
.b_btm ul li.b_4:hover p, .b_btm ul li.b_4.cur p{
	background:url(/image/b_4_h.png) no-repeat center center;
}