dl.user_tit{
	overflow:hidden; background-color:#FFF; margin-top:20px;
}
dl.user_tit dt{
	height:70px; line-height:70px; overflow:hidden; background:url(/image/d_icon.gif) no-repeat 97% center; background-color:#f7ece8;
}
dl.user_tit dt .dt_img{
	height:32px; overflow:hidden; text-align:center; float:left; margin-top:19px; margin-left:2%; margin-right:2%;
}
dl.user_tit dt .dt_img img{
	display:block; margin:0 auto;
}
dl.user_tit dt .dt_t{
	width:60%; height:70px; line-height:70px; overflow:hidden; font-size:1.8em; float:left;
}
dl.user_tit dd{
	height:59px; line-height:59px; overflow:hidden; background:url(/image/e_icon.gif) no-repeat 96% center; text-indent:9%; font-size:1.5em; border-bottom:1px dotted #b6b6b6;
}