.user_bg{
	width:100%; height:270px; overflow:hidden; background:url(/image/u_bg.jpg) no-repeat left top;
}
.user_t{
	height:191px; overflow:hidden;
}
.user_t table{
    border:0; width:93%; color:#000; margin:45px auto; border-spacing: 0; border-collapse: collapse;
}
.user_b table{
    border:0; width:100%; height:65px; color:#000; text-align:center; margin-top:8px ;  border-spacing: 0; border-collapse: collapse;
}
.u_header_bg{
	width:100px; height:100px; overflow:hidden; background:url(/image/h_bg.png) repeat center top; margin-left:0; border-radius:2px; -mos-border-radius:2px; -webkit-border-radius:2px;
}
.u_header{
	width:90px; height:90px; overflow:hidden; margin:5px auto; border-radius:2px; -mos-border-radius:2px; -webkit-border-radius:2px;
}
.u_header img{
    width:7rem; height:7rem
}
.user_t td{
	color:#000; font-size:1.3em;
}
.user_t td span{
	color:#000; font-size:1.3em;
}
.acc_btn{
	height:35px; line-height:35px; overflow:hidden; background:url(/image/in_bg.png) repeat center top; color:#222222; font-size:1.1em; text-align:center; float:right; padding:0 3%; border-radius:20px; -mos-border-radius:20px; -webkit-border-radius:20px;
}
.acc_btn a{
	color:#222222;
}
.user_b{
	height:79px; overflow:hidden;
}
.user_b td{
	color:#000; text-align:center; font-size:1.3em;
}