.back_bg{
	width:100%; height:35px; background-color:#ff4e00; color:#FFF; font-size:1.2em; padding:15px 0; position:relative;
}
.backhome{
	width:10%; overflow:hidden; float:left; margin-left:4%;
}
.backhome img{
	max-width:100%;
}
.pos{
	width:75%; overflow:hidden; line-height:1.3em; text-indent:20%; color:#FFF; font-size:1.6em; float:right;margin-right: 5%;
}

.n_arrow{
	width:32px; height:26px; overflow:hidden; position:absolute; top:30%; right:3%;
}
.n_arrow a{
	width:100%; height:100%; overflow:hidden; float:left; display:block;
}
.n_arrow img{
	width:100%;
}
.nav_con{
	width:100%; overflow:hidden; background-color:#555555; position:absolute; left:0; top:65px; z-index:500; display:none;
}
.nav_con ul li{
	width:25%; height:88px; overflow:hidden; color:#FFF; font-size:1.1em; text-align:center; float:left;
}
.nav_con ul li a{
	width:100%; height:88px; overflow:hidden; color:#FFF; float:left; text-align:center;
}
.nav_con ul li a:hover, .nav_con ul li:hover a, .nav_con ul li.cur a{
	color:#FFF; background-color:#3a3a3a;
}
.nav_con ul li p{
	height:40px; overflow:hidden; margin:5% auto 3% auto; padding:0;
}
.nav_con ul li.b_1 p{
	background:url(/image/n_1.png) no-repeat center center;
}	
.nav_con ul li.b_2 p{
	background:url(/image/n_2.png) no-repeat center center;
}
.nav_con ul li.b_3 p{
	background:url(/image/n_3.png) no-repeat center center;
}
.nav_con ul li.b_4 p{
	background:url(/image/n_4.png) no-repeat center center;
}