.back_bg{
	width:100%; height:35px; background-color:#ff4e00; color:#FFF; font-size:1.2em; padding:15px 0; position:relative;top: 0;
}
.back_bg td{
    color:#FFF;
}
.back_bg td:nth-of-type(1){
    text-align:left;
}
.back_bg td:nth-of-type(2){
    text-align:center; font-size:1.2em;
}
.back_bg td:nth-of-type(3){
    text-align:right;
}
.back_bg td a{
	color:#FFF;
}
.back_bg table{
    border:0; width:92%; color:#FFF; margin:0 auto; border-spacing: 0; border-collapse: collapse;
}