.header{
	width:100%; overflow:hidden; position:relative;height: 50px;
}
.header .top{
	 height:50px; overflow:hidden; background: #F60; position: relative;; left:0; top:0; z-index:900; padding:1% 0;display: flex;width: 100%; margin-bottom: 50px;
}
#logo{
	width:14.5%; overflow:hidden; float:left; margin-left:4%; margin-top:1%;
}
#logo img{
	width:100%;
}

.search{
	width:56%; height:45px; overflow:hidden; background-color:#FFF; float:left; margin-left:4%; border-radius:30px; -mos-border-radius:30px; -webkit-border-radius:30px;
}
.s_ipt{
	width:75%; height:45px; line-height:45px\9px; overflow:hidden; color:#888888; font-size:1.2em; font-family:"Microsoft YaHei"; float:left; margin-left:5%; border:0;
}
.s_btn{
	width:28px; height:28px; overflow:hidden; background:url(/image/s_btn.png) no-repeat center top; float:right; margin-right:3%; margin-top:8px;
}

.t_log{
	width:50px; height:45px; line-height:45px; text-align:right; color:#FFF; font-size:1.3em; float:right; margin-right:4%;
}
.t_log a{
	color:#FFF;
}


.menu{
	width:50%; overflow:hidden; float:right;
}
.menu ul li{
	width:50%; line-height:50px; overflow:hidden; background:url(/images/line.gif) no-repeat right center; color:#FFF; font-size:1.8em; text-align:center; float:left;
}
.menu ul li a{
	width:100%; line-height:50px; overflow:hidden; color:#FFF; text-align:center; float:left;
}